.level-description {
  width: 420px;
}

.input-container > p:nth-child(2) {
  margin: 5px 0;
}

.buttons {
  margin: 40px 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: center;
}

.buttons button {
  margin: 0 10px;
  width: 160px;
  cursor: pointer;

  align-self: center;

  font-size: 20px;
  color: var(--neon);
  text-shadow: 0px 0px 5px var(--neon);

  background-color: transparent;

  border-radius: 10px;
  border-width: 3px;
  border-color: var(--neon);
  border-style: solid;
  box-shadow: inset 0 0 0.5em var(--neon), 0 0 0.5em var(--neon);
  padding: 20px;
}

.buttons button.arrow {
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.help-buton {
  cursor: pointer;
  color: lightgray;
  background-color: transparent;
  border: none;
  font-size: 16px;
  margin: 10px 0;
  padding: 10px 20px;
  width: max-content;
  align-self: center;


  border-radius: 10px;
  border-color: #303030;
  border-style: solid;
  box-shadow: inset 0 0 0.5em #303030, 0 0 0.5em #303030;
}
